
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
}
h1, h2, h3 {
  cursor: pointer;
}
h1 {
  font-size: 5em;
  margin-top: 20px;
}
h2 {
  font-size: 4em;
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
  margin-left: 4px;
  margin-right: 4px;
}
h3 {
  font-size: 3em;
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
}
#spacer {
  width: 80px;
}
.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}



